.login {
  background-color: #fdeff3;
  border-radius: 6px;
  box-shadow: 0 0px 8px rgba(0, 0, 0, 0.7);
  width: 350px;
  height: fit-content;
  padding-bottom: 2.5%;
  padding-top: 1%;
  margin: 5% auto;
}

.login__signIn {
  position: relative;
  top: 5px;
  font-size: 20px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 22.5px;
}

.login__password,
.login__username {
  border-radius: 6px;
  margin: 20px;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.login__pass-input,
.login__user-input {
  margin-top: 5px;
  margin-bottom: 0;
  border-radius: 20px;
  height: 30px;
  border: 1px solid #000000;
  padding: 15px 15px;
  outline: none;
}

.login__pass-title,
.login__user-title {
  font-weight: 700;
}

.login__login {
  display: flex;
  flex-direction: column;
  margin: 0 20px 5px 20px;
}

.login__login-button {
  padding: 7.5px 0;
  background-color: rgb(255, 44, 79);
  border-radius: 6px;
  border: 1px solid red;
  font-weight: 700;
  color: white;
}

.login__login-button:nth-child(2) {
  margin-top: 10px;
}

.login__login-button:hover {
  background-color: #ffd3da;
  box-sizing: content-box;
  border: 1px solid red;
  color: rgb(255, 0, 0);
  cursor: pointer;
}

.login__register {
  font-size: 12px;
  margin-bottom: 10px;
}

.inline-sign {
  color: red;
  font-weight: 700;
}

h1 {
  text-align: center;
}
